html,
body {
  margin: 0;
}

.header {
  background-color: rgb(26, 30, 31);
  padding: 1%;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header-menu {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  list-style-type: none;
  color: white;
  margin-right: 11%;
  gap: 3rem;
}

#academy {
  padding-right: 33%;
  font-size: 22px;
  font-weight: 700;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: -20px;
}

#academy img {
  height: 60px;
  width: 60px;
  margin-bottom: -20px;
}
