.courses-container {
  background-color: rgb(22, 22, 22);
}

.courses-header {
  font-size: 24px;
  padding-left: 11%;
  padding-top: 2%;
  color: rgb(223, 219, 219);
  margin: 0;
}

.courses {
  display: grid;
  grid-template-columns: repeat(3, minmax(200px, 1fr));
  grid-template-rows: repeat(3, minmax(420px, 1fr));
  gap: 20px;
  padding: 60px 130px;
  color: rgb(223, 219, 219);
}

.grid-item {
  /* background-color: rgb(22, 22, 22); */
  /* color: white; */
  width: 70%;
  padding: 40px;
  border: solid;
  border-radius: 8px;
  border-color: rgb(63, 64, 64);
  border-width: 0.5px;
}

.grid-item img {
  height: 50px;
  width: 50px;
}
